// BASIC
import React, { Component } from 'react'
import styled, {keyframes, css as style} from 'styled-components'
// ICONS
import {MdKeyboardArrowLeft, MdKeyboardArrowRight} from 'react-icons/md'
import {TiArrowSortedDown, TiArrowSortedUp} from 'react-icons/ti'
// IMAGES
import logo from '../../Images/logo.png'
import avatar from '../../Images/avatar.jpg'
import messenger from '../../Images/messenger.svg'
import scroll from '../../Images/scroll.svg'
import easyword from '../../Images/projects/easyword.png'
import easycalc from '../../Images/projects/easycalc.png'
import easymath from '../../Images/projects/easymath.png'
// HTML
import html from '../../Images/experience/html/html.svg'
import gmail from '../../Images/experience/html/gmail.svg'
// CSS
import css from '../../Images/experience/css/css.svg'
// JS
import js from '../../Images/experience/js/js.svg'
// PYTHON
import python from '../../Images/experience/python/python.svg'
// OFFICE
import excel from '../../Images/experience/office/excel.svg'
// C++
import cpp from '../../Images/experience/cpp/cpp.svg'

const AppComponent = styled.div`
	width: 100vw;
	height: 100vh;
	max-width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 1px;
	}
	::-webkit-scrollbar-thumb {
		background-color: var(--color-main);
	}
`
const Welcome = styled.header`
	min-height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	/* min-height: calc(370px + 45vw); */
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	@media (max-height: 650px) and (max-width: 800px) {
		min-height: 600px;
		/* min-height: 100vh; */
		/* background-color: #fff; */
	}
	@media (max-height: 650px) and (min-width: 800px) {
		min-height: 650px;
		/* min-height: 100vh; */
		/* background-color: #fff; */
	}
	@media (max-width: 400px) {
		min-height: 100vh;
	}
`
const fade = keyframes`
	from {
		opacity: 0;
		/* transform: scale(0.8) */
	}
	to {
		opacity: 1;
		/* transform: scale(1) */
	}
`
const Avatar = styled.div`
	width: 250px;
	height: 250px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin: 10px;
	animation: ${fade} 0.3s ease;
	@media (min-width: 800px) {
		width: 300px;
		height: 300px;
	}
`
const Logo = styled.img`
	width: 100%;
	max-width: 450px;
	margin-bottom: 30px;
`
const scrollAnimation = keyframes`
	0% {
		opacity: 1;
		transform: translateY(0);
	}
	80% {
		opacity: 1;
		transform: translateY(10px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
`
const ScrollDown = styled.img`
	opacity: 0;
	width: 50px;
	height: 50px;
	margin-bottom: 50px;
	transition: opacity 0.3s ease;
	animation: ${scrollAnimation} 1s 1s infinite ease-out;
	:hover {
		cursor: pointer;
	}
`
const Content = styled.main``
const SectionTitle = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 30px 0;
`
const Title = styled.h1`
	text-align: center;
	font-size: 30px;
	margin: 20px 0 10px 0;
`
const Line = styled.hr`
	width: 100%;
	max-width: 1000px;
	border: none;
	border-top: 4px solid var(--color-main);
	border-radius: 2px;
	margin: 0 0 0px 0;
`
const Projects = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
`
const ProjectLink = styled.a`
	border-radius: 100%;
	margin: 30px;
`
const Project = styled.img`
	width: 100px;
	height: 100px;
	border-radius: 100%;
	transition: all 0.3s ease;
	:hover {
		box-shadow: 0 0 20px var(--color-primary);
	}
`
const Skills = styled.article`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
`
const Cards = styled.section`
	position: relative;
	width: 90vw;
	max-width: 1000px;
	display: flex;
	justify-content: space-evenly;
`
const Card = styled.div`
	width: 150px;
	height: 200px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	/* margin: 20px; */
	padding: 20px;
	text-align: center;
	overflow: hidden;
	transition: all 0.3s ease;
	opacity: 0.5;
	:hover {
		cursor: pointer;
		opacity: 1;
	}
	${props =>
		props.active &&
		style`
			opacity: 1;
		`
	}
`
const Arrow = styled.div`
	position: absolute;
	top: 20%;
	/* transform: translateY(50%); */
	font-size: 50px;
	color: var(--color-main);
	:first-child {
		left: 0;
	}
	:last-child {
		right: 0;
	}
	:hover {
		cursor: pointer;
	}
`
const LangLogo = styled.img`
	display: block;
	width: 80px;
	@media (min-width: 500px) {
		width: 100px;
	}
`
const Lang = styled.strong`
	font-size: 25px;
	margin: 20px 0;
`
const LangLetter = styled.span`
	color: ${props => props.color};
`
const LongName = styled.span`
	display: none;
	@media (min-width: 500px) {
		display: inline;
	}
`
const SkillsDescription = styled.div`
	width: 100%;
	max-width: 1000px;
	margin: 0;
	padding: 30px;
	font-size: 18px;
	text-align: center;
	border: 2px solid var(--color-main);
	border-radius: 20px;
	@media (min-width: 500px) {
		margin: 20px 0;
	}
`
const ExpImage = styled.img`
	width: 100px;
	height: 100px;
	margin: 20px;
`
const ExpDescription = styled.div`
	width: 100%;
`
const BrowserLink = styled.a`
	color: var(--color-main);
	text-decoration: none;
	font-weight: bold;
	transition: opacity 0.2s ease;
	:hover {
		opacity: 0.5;
	}
`
const Footer = styled.footer`
	text-align: center;
	margin-top: 30px;
`
const ScrollButton = styled.div`
	position: absolute;
	bottom: 20px;
	right: 10px;
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-main);
	border-radius: 100%;
	font-size: 25px;
	color: var(--color-bg);
	animation: ${fade} 0.3s ease;
	:hover {
		cursor: pointer;
	}
`
const Messenger = styled.img`
	position: absolute;
	bottom: 20px;
	left: 10px;
	width: 50px;
	height: 50px;
	animation: ${fade} 0.3s ease;
`

class App extends Component {
	state = {
		scrollToIndex: 0,
		scrolled: false,
		mainExp: {
			html: true,
			css: false,
			js: false
		}
	}
	componentDidMount() {
		window.addEventListener('resize', this.check)
	}
	check = () => {
		const page = document.getElementById('page')
		if (window.innerHeight < 650 && window.innerWidth < 800) {
			if (page.scrollTop > 630 && !this.state.scrolled) {
				this.setState({scrolled: true})
			}
			else if (page.scrollTop < 630 && this.state.scrolled) {
				this.setState({scrolled: false})
			}
		}
		// else if (window.innerHeight < 650 && window.innerWidth > 800) {
		else if (window.innerHeight < 650 && window.innerWidth > 800) {
			if (page.scrollTop > 680 && !this.state.scrolled) {
				this.setState({scrolled: true})
			}
			else if (page.scrollTop < 680 && this.state.scrolled) {
				this.setState({scrolled: false})
			}
		}
		// if (window.innerHeight > 650 && window.innerWidth < 800) {
		else {
			if (page.scrollTop > window.innerHeight + 30 && !this.state.scrolled) {
				this.setState({scrolled: true})
			}
			else if (page.scrollTop < window.innerHeight + 30 && this.state.scrolled) {
				this.setState({scrolled: false})
			}
		}

		const elements = document.getElementsByClassName('title');
		const elementTop1 = elements[0].getBoundingClientRect().top + page.scrollTop
		const elementTop2 = elements[1].getBoundingClientRect().top + page.scrollTop
		const elementTop3 = elements[2].getBoundingClientRect().top + page.scrollTop
		if (page.scrollTop < elementTop1 && this.state.scrollToIndex !== 0) {
			this.setState({scrollToIndex: 0})
		}
		else if (page.scrollTop >= elementTop1 && page.scrollTop < elementTop2 && this.state.scrollToIndex !== 1) {
			this.setState({scrollToIndex: 1})
		}
		else if (page.scrollTop >= elementTop2 && page.scrollTop < elementTop3 && this.state.scrollToIndex !== 2) {
			this.setState({scrollToIndex: 2})
		}
		else if (page.scrollTop >= elementTop3) {
			this.setState({scrollToIndex: 'up'})
		}
	}
	scrollDown = () => {
		const elements = document.getElementsByClassName('title');
		const page = document.getElementById('page')
		if (typeof(this.state.scrollToIndex) === 'number') {
			elements[this.state.scrollToIndex].scrollIntoView({behavior: 'smooth', block: 'start'})
		}
		else {
			page.scrollTo({top: 0, behavior: 'smooth'})
		}
	}
	render() {
		return (
				<AppComponent id="page" onScroll={this.check}>
					<Welcome>
						<Avatar src={avatar} />
						<Logo src={logo} />
						<ScrollDown src={scroll} onClick={this.scrollDown} />
					</Welcome>
					<Content>
						<Skills>
							<SectionTitle className="title">
								<Title>Moje Umiejętności</Title>
								<Line />
							</SectionTitle>
							<Cards>
								{/* {!this.state.mainExp.html && <Arrow>
									<MdKeyboardArrowLeft />
								</Arrow>} */}
								<Card id="html" active={this.state.mainExp.html}>
									<LangLogo src={html} />
									<Lang><LangLetter color="#F16529">H</LangLetter>tml</Lang>
								</Card>
								<Card id="css" active={this.state.mainExp.css}>
									<LangLogo src={css} />
									<Lang><LangLetter color="#3C9CD7">C</LangLetter>SS</Lang>
								</Card>
								<Card id="js" active={this.state.mainExp.js}>
									<LangLogo src={js} />
									<Lang><LangLetter color="#F1BF22">J</LangLetter><LongName>ava</LongName><LangLetter color="#F1BF22">S</LangLetter><LongName>cript</LongName></Lang>
								</Card>
								{/* {!this.state.mainExp.js && <Arrow>
									<MdKeyboardArrowRight onClick />
								</Arrow>} */}
							</Cards>
							<SkillsDescription>
									<ExpImage src={html} />
									<ExpDescription>Tworzę strony, oraz aplikacje internetowe zgodne z najnowszym standardem HTML5. Tworzone przeze mnie projekty są w pełni semantyczne, a co za tym idzie nowocześniejsze i lepiej współpracują z innymi urządzeniami</ExpDescription>
									<ExpImage src={gmail} />
									<ExpDescription>Mam doświadczenie z tworzeniem automatycznych szablonów e-mail. Swoje usługi zaoferowałem między firmie <BrowserLink href="https://magicalgoods.com.pl" target="_blank">Magical Goods</BrowserLink></ExpDescription>
							</SkillsDescription>
							<SectionTitle className="title">
								<Title>Inne</Title>
								<Line />
							</SectionTitle>
							<Cards>
								<Card>
									<LangLogo src={python} />
									<Lang><LangLetter color="#387EB8">P</LangLetter><LangLetter color="#FFE052">y</LangLetter>thon</Lang>
								</Card>
								<Card>
									<LangLogo src={cpp} />
									<Lang><LangLetter color="#1B598E">C</LangLetter>++</Lang>
								</Card>
								<Card>
									<LangLogo src={excel} />
									<Lang><LangLetter color="#107C41">V</LangLetter>BA</Lang>
								</Card>
							</Cards>
							<SkillsDescription>
								Lorem, ipsum dolor sit amet consectetur adipisicing elit. Incidunt voluptate quas dicta accusamus aperiam quam dolores voluptatibus ea in, dolorem illo, soluta sed et magnam consequuntur officia, architecto itaque iste.
							</SkillsDescription>
						</Skills>
						<Projects>
							<SectionTitle className="title">
								<Title>Moje projekty</Title>
								<Line />
							</SectionTitle>
							<ProjectLink href="https://easyword123.pl" target="_blank">
								<Project src={easyword} />
							</ProjectLink>
							<p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Magnam accusantium beatae eaque laudantium? Praesentium repellendus vel laborum fuga nobis velit quos molestias, minus vitae. Quibusdam quam impedit recusandae exercitationem nesciunt.</p>
							<ProjectLink href="https://easycalc.web.app" target="_blank">
								<Project src={easycalc} />
							</ProjectLink>
							<p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Magnam accusantium beatae eaque laudantium? Praesentium repellendus vel laborum fuga nobis velit quos molestias, minus vitae. Quibusdam quam impedit recusandae exercitationem nesciunt.</p>
							<ProjectLink href="https://easyvalue.web.app" target="_blank">
								<Project src={easymath} />
							</ProjectLink>
							<p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Magnam accusantium beatae eaque laudantium? Praesentium repellendus vel laborum fuga nobis velit quos molestias, minus vitae. Quibusdam quam impedit recusandae exercitationem nesciunt.</p>
						</Projects>
						<Footer>&copy; Jakub Schulz 2020</Footer>
					</Content>
					{this.state.scrolled && <ScrollButton onClick={this.scrollDown}>{this.state.scrollToIndex === 'up' ? <TiArrowSortedUp /> : <TiArrowSortedDown />}</ScrollButton>}
					{this.state.scrolled && <BrowserLink href="https://m.me/szulcus" target="_blank">
						<Messenger src={messenger} />
					</BrowserLink>}
				</AppComponent>
		);
	}
}

export default App